body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
h1 {
  font-size: 50px;
}
.ant-layout {
  height: 100vh;
  text-align: center;
}
.do-hyeon {
  font-family: 'Do Hyeon', sans-serif;
}
.page-wrap {
  height: 100%;
}
.form-wrap {
  margin-top: 20px;
}
.form-wrap .ant-select,
.form-wrap .ant-btn {
  margin-right: 10px;
  border-radius: 0;
}
.ant-layout-header {
  background: -webkit-linear-gradient(45deg, #2355c2, #56a856) !important;
  background: linear-gradient(45deg, #2355c2, #56a856) !important;
  height: 100px !important;
  line-height: 100px !important;
  text-align: left;
}
.ant-layout-header button {
  border-radius: 0;
  background-color: #56a856;
}
.list {
  text-align: left;
  font-size: 20px;
  padding-top: 15px;
  padding-bottom: 15px;
  border-top: 1px solid #ececec;
  border-bottom: 1px solid #ececec;
}
